.PlaceBlock
{
    
    height: 50px;
    background: linear-gradient(45deg, #240b41, #4c0a53);

    border-radius: 22px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .25);

    margin-top: 10px;
}

.PlaceItemImg
{
    float: left;

    width: 40px;
    height: 40px;
    border-radius: 50px;

    margin-top: 5px;
    margin-left: 5px;
    background: #47197c;
}

.PlaceText
{
    float: left;
}

.PlaceItemText
{
    margin-left: 15px;
    margin-top: 10px;
    font-weight: 600;
    font-size: 14px;

    color: #ffffff;
}

.PlaceItemTextDesc
{
    color: #a1a1a1;
    font-size: 13px;
    margin-left: 15px;
}
